import { Action, ActionCreator  } from 'redux';
import { ActionTypes } from '../actionTypes';

export interface IGetWeatherSuccessAction extends Action {
  payload: {
    sunrise: string,
    sunset: string,
  }
}

export const getWeather: ActionCreator<Action> = () => ({
  type: ActionTypes.GET_WEATHER,
})

export const getWeatherSuccess: ActionCreator<IGetWeatherSuccessAction> = (sunrise: string, sunset: string) => ({
  payload: {
    sunrise,
    sunset,
  },
  type: ActionTypes.GET_WEATHER_SUCCESS,
})



