import * as React from 'react';

import { Layout } from 'antd';
import { connect } from 'react-redux';
import { Route, RouteComponentProps, Switch, withRouter } from 'react-router-dom';
import { Dispatch } from 'redux';

import { ConditionalRoute } from './components/ConditionalRoute';
import NotFound from './components/NotFound';
import Unauthorised from './components/Unauthorised';
import Home from './containers/Home';
import Profile from './containers/Profile';

import AppHeader from './containers/AppHeader';
import Login from './containers/Login';
import MapContainer from './containers/MapContainer';
import { IApplicationState } from './store/applicationState';

import './App.css';
import { setOnline } from './store/action-creators/setOnline';

const { Content } = Layout;

interface IPropsFromState {
  isAuthenticated: boolean,
}

interface IPropsFromDispatch {
  setOnline: (isOnline: boolean) => void,
}

const mapStateToProps = (state: IApplicationState): IPropsFromState => ({
  isAuthenticated: state.isAuthenticated,
});

const mapDispatchToProps = (dispatch: Dispatch): IPropsFromDispatch => ({
  setOnline: (isOnline) => dispatch(setOnline(isOnline)),
})

class App extends React.Component<IPropsFromState & IPropsFromDispatch & RouteComponentProps> {
  public componentDidMount() {
    window.addEventListener('online', this.setOnline);
    window.addEventListener('offline', this.setOffline);
  }

  public render() {
    return (
      <div className="App">
        <Layout style={{ minHeight: '100vh' }}>
            <AppHeader />
            <Content>
              <Switch>
                <ConditionalRoute
                  exact={true}
                  path={'/'}
                  routeCondition={!this.props.isAuthenticated}
                  component={Login}
                  redirectTo={'/home'}
                />
                <ConditionalRoute
                  exact={true}
                  path={'/home'}
                  routeCondition={this.props.isAuthenticated}
                  component={Home}
                  redirectTo={'/'}
                />
                <ConditionalRoute
                  exact={true}
                  path={'/profile'}
                  routeCondition={this.props.isAuthenticated}
                  component={Profile}
                  redirectTo={'/'}
                />
                <ConditionalRoute
                  exact={true}
                  path={'/map'}
                  routeCondition={this.props.isAuthenticated}
                  component={MapContainer}
                  redirectTo={'/'}
                />
                <Route
                  exact={true}
                  path="/unauthorised"
                  component={Unauthorised}
                />
                <Route
                  component={NotFound}
                />
              </Switch>
            </Content>
          </Layout>
      </div>
    );
  }

  private setOnline = () => {
    this.setOnlineStatus(true);
  }

  private setOffline = () => {
    this.setOnlineStatus(false);
  }

  private setOnlineStatus = (isOnline: boolean) => {
    this.props.setOnline(isOnline);
  }
}

export default withRouter(connect<IPropsFromState,IPropsFromDispatch>(mapStateToProps,mapDispatchToProps)(App));



