import axios from 'axios';
import { put, takeLatest } from 'redux-saga/effects';
import { getWeatherSuccess } from '../action-creators/getWeatherInfo';
import { ActionTypes } from '../actionTypes';

export function* getWeatherRequestWatcher() {
  yield takeLatest(ActionTypes.GET_WEATHER, getWeather);
}

export function* getWeather() {
  try {
    const response = yield axios.get('https://query.yahooapis.com/v1/public/yql?q=select%20astronomy%20from%20weather.forecast%20where%20woeid%20in%20(select%20woeid%20from%20geo.places(1)%20where%20text%3D%22Singapore%2C%20sg%22)&format=json&env=store%3A%2F%2Fdatatables.org%2Falltableswithkeys');
    const astronomyResult = response.data.query.results.channel.astronomy;
    yield put(getWeatherSuccess(astronomyResult.sunrise, astronomyResult.sunset));
  } catch (err) {
    console.log(err);
  }
}


