import { Action, ActionCreator  } from 'redux';
import { ActionTypes } from '../actionTypes';

export interface ISetOnlineAction extends Action {
  payload: {
    isOnline: boolean,
  }
}

export const setOnline: ActionCreator<ISetOnlineAction> = (isOnline: boolean) => ({
  payload: {
    isOnline
  },
  type: ActionTypes.ONLINE_SET,
})



