import { all, fork } from 'redux-saga/effects';

import { getWeatherRequestWatcher } from './getWeatherSagas';
import { loginRequestWatcher } from './loginSagas';
import { logoutRequestWatcher } from './logoutSagas';
import { getLocationRequestWatcher } from './setLocationSagas';

// single entry point to start all Sagas at once
export default function *rootSaga() {
  yield all([
    fork(loginRequestWatcher),
    fork(logoutRequestWatcher),
    fork(getLocationRequestWatcher),
    fork(getWeatherRequestWatcher),
  ]);
}



