import * as React from 'react';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { getWeather } from 'src/store/action-creators/getWeatherInfo';
import { IApplicationState } from 'src/store/applicationState';

interface IPropsFromState {
  sunrise?: string,
  sunset?: string,
}

interface IPropsFromDispatch{
  getWeather: () => void,
}

const mapStateToProps = (state: IApplicationState): IPropsFromState => ({
  sunrise: state.sunrise,
  sunset: state.sunset,
})

const mapDispatchToProps = (dispatch: Dispatch) : IPropsFromDispatch => ({
  getWeather: () => dispatch(getWeather()),
});

class Home extends React.PureComponent<IPropsFromState & IPropsFromDispatch> {
  public componentDidMount() {
    this.props.getWeather();
  }

  public render() {
    return (
      <React.Fragment>
        <div>The time now is {(new Date()).toString()}</div>
        <div>Today's sunrise is {this.props.sunrise}</div>
        <div>Today's sunset is {this.props.sunset}</div>
      </React.Fragment>

    )
  }
}

export default connect<IPropsFromState,IPropsFromDispatch>(mapStateToProps,mapDispatchToProps)(Home);



