import * as React from 'react';

import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import { getLocation } from 'src/store/action-creators/setLocation';
import Map from './Map';

interface IPropsFromDispatch {
  getLocation: () => void,
}

const mapDispatchToProps = (dispatch: Dispatch): IPropsFromDispatch => ({
  getLocation: () => dispatch(getLocation()),
});

class MapContainer extends React.PureComponent<IPropsFromDispatch> {
  public componentDidMount() {
    if(navigator.geolocation) {
      this.props.getLocation();
    } else {
      console.log("Geo Location not supported by browser");
    }
  }

  public render() {
    return (
      <div style={{minHeight: 'calc(100vh - 64px)', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
        <Map
          googleMapURL={'https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places'}
          loadingElement={<div style={{ height: `100%` }} />}
          containerElement={<div style={{ height: `600px`, width: `600px` }} />}
          mapElement={<div style={{ height: `100%` }} />}
        />
      </div>
    )
  }
}

export default connect<{},IPropsFromDispatch>(undefined, mapDispatchToProps)(MapContainer);


