import * as H from 'history';
import { Action, ActionCreator  } from 'redux';

import { ActionTypes } from '../actionTypes';
import { IUser } from '../applicationState';

export interface ILoginAction extends Action {
  payload: {
    loginName: string,
    password: string,
    history: H.History,
    cookies: any,
  },
}

export interface ILoginSuccessAction extends Action {
  payload: {
    user: IUser,
  },
}

export interface ILoginFailedAction extends Action {
  payload: {
    err: string,
  }
}

export const login: ActionCreator<ILoginAction> = (loginName: string, password: string, history: H.History, cookies: any) => ({
  payload: {
    cookies,
    history,
    loginName,
    password,
  },
  type: ActionTypes.LOGIN_REQUEST,
});

export const loginSuccess: ActionCreator<ILoginSuccessAction> = (user: IUser) => ({
  payload: {
    user
  },
  type: ActionTypes.LOGIN_SUCCESS,
});

export const loginFailed: ActionCreator<ILoginFailedAction> = (err: string) => ({
  payload: {
    err
  },
  type: ActionTypes.LOGIN_FAILED,
});



