import { call, put, takeLatest } from 'redux-saga/effects';

import { getLocationFailed, getLocationSuccess } from '../action-creators/setLocation';
import { ActionTypes } from '../actionTypes';

export function* getLocationRequestWatcher() {
  yield takeLatest(ActionTypes.LOCATION_REQUEST, setLocation);
}

function* setLocation() {
  try {
    const location = yield call(getLocationPromise);
    const {latitude, longitude} = location.coords;
    yield put(getLocationSuccess([latitude,longitude]));
  } catch (err) {
    yield put(getLocationFailed(err));
  }
}

const getLocationPromise = () => new Promise((resolve, reject) => {
  navigator.geolocation.getCurrentPosition(
   location => resolve(location),
   error => reject(error),
  )
 })


