import * as React from 'react';

import { Button, Form, Icon, Input } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import * as H from 'history';
import { connect } from 'react-redux';
import { RouteComponentProps, withRouter } from 'react-router';
import { Dispatch } from 'redux';

import { login } from '../store/action-creators/login';
import { IApplicationState } from '../store/applicationState';

import '../css/Login.css';

interface ILoginFormValue {
  loginName: string,
  password: string,
}

interface IPropsFromState {
  cookies: any,
  isAuthenticated: boolean,
  isLoading: boolean,
}

interface IPropsFromDispatch {
  login: (loginName: string, password: string, history: H.History, cookies: any) => void,
}

const FormItem = Form.Item;

const mapStateToProps = (state: IApplicationState): IPropsFromState => ({
  cookies: state.cookies,
  isAuthenticated: state.isAuthenticated,
  isLoading: state.isLoading,
});

const mapDispatchToProps = (dispatch: Dispatch): IPropsFromDispatch => ({
  login: (loginName, password, history, cookies) => dispatch(login(loginName, password, history, cookies)),
});


class LoginForm extends React.Component<FormComponentProps & IPropsFromState & IPropsFromDispatch & RouteComponentProps> {
  public render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <div className='login-form-container'>
        <Form className='login-form' onSubmit={this.handleSubmit}>
          <FormItem className='login'>
            {getFieldDecorator('loginName', {
              rules: [{ required: true, message: 'Please input your login!'}]
            })(
              <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Username" autoComplete="username"/>
            )}
          </FormItem>
          <FormItem>
            {getFieldDecorator('password', {
              rules: [{ required: true, message: 'Please input your Password!' }],
            })(
              <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" autoComplete="current-password"/>
            )}
          </FormItem>
          <FormItem>
            <Button type="primary" htmlType="submit" size="large" className="login-form-button" loading={this.props.isLoading}>
              Log in
            </Button>
          </FormItem>
        </Form>
      </div>
    )
  };

  private handleSubmit = (event: React.FormEvent): void => {
    event.preventDefault();

    this.props.form.validateFields((err, values: ILoginFormValue) => {
      if (!err) {
        this.props.login(values.loginName, values.password, this.props.history, this.props.cookies);
      }
    });
  }
}

export default withRouter(connect<IPropsFromState, IPropsFromDispatch>(mapStateToProps, mapDispatchToProps)(Form.create()(LoginForm)));



