import { Action, ActionCreator } from 'redux';

import { ActionTypes } from '../actionTypes';

export interface ISetLocationAction extends Action {
  payload: {
    location: [number,number],
  }
}

export interface IGetLocationFailedAction extends Action {
  err: string,
}

export const getLocation: ActionCreator<Action> = () => ({
  type: ActionTypes.LOCATION_REQUEST,
});

export const getLocationSuccess: ActionCreator<ISetLocationAction> = (location: [number,number]) => ({
  payload: {
    location,
  },
  type: ActionTypes.LOCATION_SUCCESS
})

export const getLocationFailed: ActionCreator<IGetLocationFailedAction> = (err: string) => ({
  err,
  type: ActionTypes.LOCATION_FAILED,
});



