import { Action, ActionCreator  } from 'redux';

import { ActionTypes } from '../actionTypes';

export interface IHeaderSelectionAction extends Action {
  payload: {
    selectedKey: string,
  }
}

export const headerSelection: ActionCreator<IHeaderSelectionAction> = (selectedKey: string) => ({
  payload: {
    selectedKey
  },
  type: ActionTypes.UI_HEADER_SELECTION
});



