import { delay } from 'redux-saga';
import { put, takeLatest } from 'redux-saga/effects';
import Cookies from 'universal-cookie';

import { ILoginAction, loginFailed, loginSuccess } from '../action-creators/login';
import { ActionTypes } from '../actionTypes';

export function* loginRequestWatcher() {
  yield takeLatest(ActionTypes.LOGIN_REQUEST, login);
}

function* login(action: ILoginAction) {
  try {
    yield delay(1000);
    yield put(loginSuccess({id: 1, profileName: 'toon pang'}));
    (action.payload.cookies as Cookies).set('isAuthenticated', true, {maxAge: 31536000});
    action.payload.history.push('/home');
  } catch (err) {
    console.log(err);
    yield put(loginFailed(err));
  }
}


