import * as H from 'history';
import { Action, ActionCreator } from 'redux';

import { ActionTypes } from '../actionTypes';

export interface ILogoutAction extends Action {
  payload: {
    cookies: any,
    history: H.History,
  }
}

export interface ILogoutFailedAction extends Action {
  payload: {
    err: string,
  }
}

export const logout: ActionCreator<ILogoutAction> = (history: H.History, cookies: any) => ({
  payload: {
    cookies,
    history,
  },
  type: ActionTypes.LOGOUT_REQUEST
});

export const logoutSuccess: ActionCreator<Action> = () => ({
  type: ActionTypes.LOGOUT_SUCCESS,
});

export const logoutFailed: ActionCreator<ILogoutFailedAction> = (err: string) => ({
  payload: {
    err
  },
  type: ActionTypes.LOGOUT_FAILED,
});



