import * as React from 'react';

import { GoogleMap, Marker, withGoogleMap, withScriptjs } from "react-google-maps"
import { connect } from 'react-redux';

import { IApplicationState } from 'src/store/applicationState';

interface IPropsFromState {
  location?: [number,number],
}

const mapStateToProps = (state: IApplicationState): IPropsFromState => ({
  location: state.currentLocation,
});

class Map extends React.PureComponent<IPropsFromState> {
  public static defaultProps: IPropsFromState = {
    location: [1.3521, 103.8198],
  };

  public render() {
    return (
      <GoogleMap
        defaultZoom={14}
        center={{ lat: this.props.location![0], lng: this.props.location![1] }}
      >
        <Marker position={{ lat: this.props.location![0], lng: this.props.location![1] }}/>
      </GoogleMap>
    )
  }
}

export default connect<IPropsFromState>(mapStateToProps)(withScriptjs(withGoogleMap(Map)));



