import * as React from 'react';

import { Button } from 'antd';
import { History } from 'history';
import { connect } from 'react-redux';
import { RouteComponentProps, withRouter } from 'react-router';
import { Dispatch } from 'redux';

import { logout } from 'src/store/action-creators/logout';
import { IApplicationState } from 'src/store/applicationState';


interface IPropsFromState {
  cookies: any,
  isLoading: boolean,
}

interface IPropsFromDispatch {
  logout: (history: History, cookies: any) => void,
}

const mapStateToProps = (state: IApplicationState): IPropsFromState => ({
  cookies: state.cookies,
  isLoading: state.isLoading,
});

const mapDispatchToProps = (dispatch: Dispatch): IPropsFromDispatch => ({
  logout: (history, cookies) => dispatch(logout(history, cookies)),
});

class Profile extends React.PureComponent<IPropsFromState & IPropsFromDispatch & RouteComponentProps> {
  public render() {
    return (
      <React.Fragment>
        <div>
          <input type='file' accept='image/*'/>
        </div>
        <div>
          <Button onClick={this.handleLogOut} loading={this.props.isLoading}>Logout</Button>
        </div>
        <div>
          <a href='https://www.google.com'>Click here for external link</a>
        </div>
        <div>
          <Button onClick={this.handleExternal}>popup</Button>
        </div>
      </React.Fragment>

    )
  }

  private handleExternal = () => {
    window.open ('https://www.google.com','mywindow', 'width=350,height=250');
  }

  private handleLogOut = () => {
    this.props.logout(this.props.history, this.props.cookies);
  }
}

export default withRouter(connect<IPropsFromState,IPropsFromDispatch>(mapStateToProps, mapDispatchToProps)(Profile));



